@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);
.loader{
    width:30px;
    height:30px;
    margin:auto;
    position:relative;
 }
 
 .loader-circle{
   width:100%;
   height:100%;
   position:absolute;
 }
 
 .loader-circle::before{
    content:"";
    width:10px;
    height:10px;
    border-radius:100%;
    background:var(--tertiary-color-shade-1);
    display:block;
 }
 
 .c1{
   transform:rotate(0deg);
   animation:rotate 0.8s infinite linear;
 }
 .c2{
   transform:rotate(45deg);
   animation:rotate 0.8s 0.1s infinite linear;
 
 }
 .c3{
   transform:rotate(90deg);
   animation:rotate 0.8s 0.2s infinite linear;
 
 }
 .c4{
   transform:rotate(135deg);
   animation:rotate 0.8s 0.3s infinite linear;
 
 }
 .c5{
   transform:rotate(180deg);
   animation:rotate 0.8s 0.4s infinite linear;
 
 }
 .c6{
   transform:rotate(225deg);
   animation:rotate 0.8s 0.5s infinite linear;
 }
 
 .c7{
   transform:rotate(270deg);
   animation:rotate 0.8s 0.6s infinite linear;
 }
 .c8{
   transform:rotate(315deg);
   animation:rotate 0.8s 0.7s infinite linear;
 }
 .c9{
   transform:rotate(360deg);
   animation:rotate 0.8s 0.8s infinite linear;
 }
 
 @keyframes rotate{
    from {
      opacity:0;
    }
    to{
      opacity:1;
    }
 }
.route__not__found{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:70vh;
    font-size: 1rem;
    text-align: center;
    color: var(--primary-text-color);
    flex-direction: column;
  }
  
  .route__not__found .go__to__home__btn{
     cursor: pointer;
     margin-top: 0.5rem;
     background:var(--primary-text-color);
     color: var(--primary-color);
     border: none;
     font-weight:600;
     padding:0.5rem 1rem;
     border-radius: 5px;
  }
.fa-check{
    color:var(--success-color);
}

.fa-times-circle{
    color:var(--danger-color);
}

.feedback{
    position:absolute;
    top:10px;
    right:10px;
    display:flex;
}

.fa-eye,.fa-eye-slash{
    margin-left:0.2rem;
    cursor:pointer;
}
.form__group{
    position:relative;
}

.form__label{
    position:absolute;
    color:var(--tertiary-color-shade-1);
    top:-7px;
    left:9px;
    font-size:0.6rem;
    transform: translateY(10px);
    animation: gototop 0.1s 1 linear;
}

@keyframes gototop{
    from{
        transform: translateY(20px);
    }
    to {
        transform: translateY(10px);
    }
}

.form__control{
    box-sizing:border-box;
    padding:0.5rem;
    padding-top:0.8rem;
    width:100%;
    border:1px solid var(--tertiary-color);
    margin-bottom:0.5rem;
    border-radius:2px;
    font-size:0.8rem;
    background:var(--primary-color);
    transition:all 0.5s linear;
}

.form__control:focus{
    outline:none;
    border:1px solid var(--tertiary-color-shade-1);
}

.submit__btn{
    padding:0.5rem;
    background:var(--primary-btn-color);
    color:var(--secondary-color);
    font-weight:700;
    border:none;
    border-radius:5px;
    cursor:pointer;
}

.submit__btn:disabled{
    background:var(--primary-btn-disabled-color);
    cursor:default;
}
.auth__container{
