   flex-direction:column;
   width:100%;
   justify-content:center;
   align-items:center;
   min-height:100vh;
}

.auth__container .section1,.auth__container .section2{
    border:1px solid var(--tertiary-color);
    display:flex;
    flex-direction:column;
    width:290px;
    background:var(--secondary-color);
    padding:1.5rem 2rem;
    margin:0.4rem 0;
}

.auth__container .insta__logo{
  margin: 22px auto 12px;
  background-image: url("https://www.instagram.com/static/bundles/es6/sprite_core_b20f2a3cd7e4.png/b20f2a3cd7e4.png");
  background-position: -98px 0;
  height: 51px;
  width: 177px;
  overflow: hidden;
}

.auth__container .signup__title{
    color:var(--tertiary-color-shade-1);
    text-align:center;
    font-size:0.95rem;
    margin:0.5rem 0;
}

.auth__container .section2__title{
    font-size:0.8rem;
    text-align:center;
}

.auth__container .section2__title__Link{
    color:var(--primary-btn-color);
    text-decoration:none;
}

.auth__container .auth__invalid__feedback{
    text-align:center;
    margin-top:0.5rem;
    color:var(--danger-color);
}

.auth__container .auth__valid__feedback{
    text-align:center;
    margin-top:0.5rem;
    color:var(--success-color);
}

.auth__container .separator{
    display:flex;
    padding:0.5rem 0;
    align-items:center;
    font-weight:500;
    color:var(--tertiary-color-shade-1);
    font-size:0.8rem;
}

.auth__container .separator__line{
    flex:1 1;
    position:relative;
    height:1px;
    background:var(--tertiary-color);
}

.auth__container .separator__text{
    margin:0 0.5rem;
}

@media (max-width:450px){
.auth__container .section1,.auth__container .section2{
    background:var(--primary-color);
    border:1px solid var(--primary-color);
}
}

@media (max-width:350px){
    .auth__container .section1,.auth__container .section2{
        width:90%;
    }
}
.backdrop{
    position:fixed;
    top:0;
    left:0;
    min-height:100%;
    width:100%;
    z-index:1;
}
.dropbox{
    position:absolute;
    top:2.5rem;
    right:-1rem;
    width:220px;
    background:var(--secondary-color);
    border:1px solid var(--tertiary-color);
    border-radius:10px;
    box-shadow:1px 1px 3px var(--tertiary-color),-1px -1px 3px var(--tertiary-color);
    display:flex;
    flex-direction:column;
    z-index:2;
}

.dropbox::after{
    position:absolute;
    right:1.3rem;
    top:-0.5rem;
    transform:rotate(45deg);
    content:"";
    height:16px;
    width:16px;
    box-shadow:-1px -1px var(--tertiary-color);
    background:var(--secondary-color);
}

.dropbox .dropbox__btn{
    background:var(--secondary-color);
    border:none;
    display:flex;
    padding:0.8rem;
    cursor:pointer;
    border-radius:10px;
}

.dropbox .dropbox__btn:hover{
    background:var(--primary-color);
}

.dropbox .dropbox__btn:active{
    background:var(--tertiary-color-shade-2);
}

.dropbox .dropbox__btn .fas{
    margin-right:0.5rem;
}

.dropbox .logout{
    border-top:1px solid var(--tertiary-color);
    border-top-left-radius:0;
    border-top-right-radius:0;
}

@media (max-width:550px){
    .dropbox{
        display:none;
    }
}
.searchBox{
    position:absolute;
    top:2.5rem;
    right:-6.5rem;
    width:350px;
    background:var(--secondary-color);
    border:1px solid var(--tertiary-color);
    border-radius:10px;
    box-shadow:1px 1px 3px var(--tertiary-color),-1px -1px 3px var(--tertiary-color);
    display:flex;
    flex-direction:column;
    height:320px;
    padding:1rem;
    z-index:2;
    font-weight:500;
}

.searchBox::after{
    content:"";
    position:absolute;
    top:-0.5rem;
    left:48%;
    height:15px;
    width:15px;
    background:var(--secondary-color);
    border-top:1px solid var(--tertiary-color);
    border-left:1px solid var(--tertiary-color);
    transform:rotate(45deg);
}

.searchBox .searchBox__heading{
    color:var(--primary-text-color);
}

.searchBox .searchBox__empty{
    display:flex;
    justify-content:center;
    align-items:center;
    min-height:90%;
    color:var(--tertiary-color-shade-1);
    font-weight:700;
    font-size:0.8rem;
}

.searchBox .searchBox__results{
    display:flex;
    flex-direction:column;
    overflow-y:auto;
}

.searchBox__results .searchBox__result{
    cursor:pointer;
    display:flex;
    align-items:center;
    margin-bottom:1rem;
    border:none;
    background:transparent;
    text-decoration:none;
}

.searchBox__results .searchBox__result:hover{
    background:var(--primary-color);
}

.searchBox__results .avatar{
    height:45px;
    width:45px;
    border-radius:50%;
}

.searchBox__results .avatar__img{
    height:100%;
    width:100%;
    border-radius:50%;
}

.searchBox__results .result__content{
    display:flex;
    flex-direction:column;
    margin-left:1rem;
}

.searchBox__results .username{
    color:var(--primary-text-color);
}

.searchBox__results .fullname{
    color:var(--tertiary-color-shade-1);
    font-size:0.7rem;
}

.spinner__container{
    display:flex;
    justify-content:center;
    align-items:center;
    min-height:320px;
}

.searchBox ::-webkit-scrollbar {
    width:10px;
  }
  
  /* Track */
.searchBox ::-webkit-scrollbar-track {
    box-shadow:inset 0 0 5px var(--tertiary-color); 
    border-radius:10px;
  }
   
  /* Handle */
.searchBox ::-webkit-scrollbar-thumb {
    background:var(--tertiary-color-shade-1); 
    border-radius:10px;
  }
  
  /* Handle on hover */
.searchBox ::-webkit-scrollbar-thumb:hover {
    background:var(--tertiary-color-shade-1); 
}
.search__bar{
    position:relative;
    background:var(--primary-color);
}

.search__bar .search__input{
    padding:0.3rem;
    padding-left:1.2rem;
    border:1px solid var(--tertiary-color);
    border-radius:2px;
}

.search__bar .search__input:focus{
    outline:var(--tertiary-color-shade-1);
}

.search__bar .fa-search{
    position:absolute;
    left:6px;
    top:7.5px;
    font-size:0.7rem;
    color:var(--tertiary-color-shade-1);
}

@media(max-width:550px){
    .search__bar{
        display:none;
    }
}

.search__bar .search__bar__backdrop{
    top:3rem;
}
